@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
body {
  font-family: 'Poppins', sans-serif;
  font-weight: 300
}

h1,h2,h3 {
  font-weight: 300;
  margin: 30px 0;
}

img {
  width: 100%;
  margin-top: 35px;
  height: 95%;
}

.product-info{
  padding-left: 4em;
}

.vinyl-detail-back{
  display: inline-block;
  margin-right: 3em;
}

.btn{
  color: black
}

a, a:hover {
  color: inherit;
  text-decoration: none
}

.register {
  font-size: 13px;
}

.register-link {
  color: #ffc107
}

.cart-logo{
  background-color: #ffc107;
}

.navbar-color{
 background: linear-gradient(90deg,#52bcf8,#5082e4);
 margin-bottom: 30px
}

.navbar-color-second{
 background: linear-gradient(90deg,#52bcf8,#5082e4);
}

.toast{
  font-size: 20px;
}












.logonavbar{
    height: 80px;
    width: 140px;
    margin-bottom: 40px;
    margin-left: 50px
}

.navbar1{
    height: 90px
}
.vinyl-card {
    margin-bottom: 40px;
}

.vinyl-card img {
    margin-bottom: 20px;
    height: 300px;
    object-fit: cover;
}

.vinyl-card text {
    display: flex;
    flex-direction: row;
    align-content: space-around;
}

.btn-add{
    margin-bottom: 20px;
}


.carousel img{
    height: 600px;
    width: 2000px;
}

.carousel {
    margin-left: auto;
    margin-right: auto;
    max-width: 700px;
}

.carousel-back{
    background-image: url( /static/media/manuel-sardo-HwFv8EZYC_o-unsplash.a6abc625.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 1800px;
}

.teaser {
    position: relative;
    display: flex;
    flex-direction: row;
    overflow: hidden;
    margin-bottom: 200px;
    background-color: #f8f7f5;
    letter-spacing: 1px;
    height: 700px;
}

.fluid{
    padding-left: 60px;
    padding-right: 60px;
    max-width: 1600px;
}

.logo-index{
    height: 300px;
    width: 300px;
    margin-left: 500px
}
